/**
 * The menu is technically the header of the site, it contains the styles for the drop down of the Search Bar.
 *
 */

$lineHeightForNav: 70px;
$lineHeightForSearch: 20px;


header.menu {

    // ...
    width: 100%;
    position: relative;
    z-index: 10;

    div.menu-container {
        width: 100%;
        background-color: black;
        padding: 0 10px;
        text-align: left;
        
        div.menu-holder {
            position: relative;
            width: 100%;
            max-width: $siteWidth;
            margin: 0 auto;
        }

        table {
            display: inline-block;
            margin-left: 60px;

            i { 
                color: grey; 
                font-size: 22px;
                line-height: $lineHeightForNav;
            }
            i.search {
                position: absolute;
                right: 10px;
                top: 0px;
            }
            tr td.logo {
                width: 30%;
                background-image: url('/img/logo.jpg');
                background-size: 160px 90px;
                background-repeat: no-repeat;
            }
            a.nav-item {
                font-family: Helvetica, Arial, Sans-Serif;
                font-size: 17px;
                line-height: $lineHeightForNav;
                text-transform: uppercase;
                text-decoration: none;
                white-space: nowrap;
                border-bottom: 1px solid transparent;
                color: #FFF;
                margin-right: 20px;
            }

            input.nav-search {
                background-color: #000;
                border:none;
                border-bottom: 1px solid #FFF;
                color:#FFF;
                display: inline-block;
                font-family: Helvetica, Arial, Sans-Serif;
                font-size: 17px;
            }
        }
    }

    div.search-container {
        position: absolute;
        background-color: white;
        z-index: 20;
        box-shadow: 0 0 10px rgba(0,0,0,0.1);
        width: 100%;
        height: 180px;
        padding: 5px;
        background-color: white;
        border-bottom: 1px solid #CCC;
        display: none;
        &.showing { display: block; }
        
        input[type='text'] {
            min-width: 100%;
            font-size: 36px;
            line-height: $lineHeightForSearch;
            border: 1px solid #CCC;
            padding: 0 10px;
        }

        i.close {
            position: absolute;
            right: 10px;
            top: 5px;
            font-size: 20px;
            line-height: $lineHeightForSearch;
        }

        div.result-item{
            height: 160px;
            width: 200px;
            float: left;
            margin: 5px;
            border: #000 1px solid;
            font-family: Helvetica, Arial, Sans-Serif;
            font-size: 11px;

            img {
                height: 150px;
                width:70px;
            }

            img.addCart {
                height: 30px;
                width: 30px;
                float: right;
                margin-right: 2px;
                margin-bottom: 2px;
                cursor: pointer;
            }
        }
    }
}